body {
    font-family: 'Roboto', sans-serif;
    background-color: #010001;
    color: #FFFFFF;
    margin: 0;
    padding-top: 53px;
}

.header {
    max-width: 1230px;
    width: 90%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 120px;
}

.list {
    padding: 0;
    display: flex;
    list-style: none;
}

.item-link {
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
    text-decoration: none;
    margin-left: 10px;
    margin-right: 10px;
}

.secondary-nav .item-link {
    color: #808080;
}

.main {
    text-align: center;
}

.title {
    font-style: normal;
    font-weight: normal;
    font-size: 64px;
    line-height: 75px;
    color: #FFFFFF;
    margin-bottom: 26px;
    margin-top: 0;
}

.description {
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    text-transform: lowercase;
    color: #FFFFFF;
    max-width: 268px;
    margin: auto;

    &:after {
        content: '';
        display: block;
        width: 1px;
        height: 60px;
        background-color: #FFFFFF;
        margin: auto;
        margin-top: 26px;
    }
}

.video {
    height: 750px;
    max-width: 1230px;
    width: 90%;
    margin: auto;
}

.features {
    position: relative;
    max-width: 1230px;
    width: 90%;
    margin: auto;
    padding: 100px;
    margin: 0;
}

.features-title {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 140%;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-bottom: 45px;
}

.features-text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 29px;
    color: #FFFFFF;
    max-width: 490px;
}

.table {
    margin-bottom: 200px;
}

.table-column {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 179.69%;
    color: #FFFFFF;
    width: 300px;
    padding-top: 7px;
    padding-bottom: 7px;
}

.rocket {
    position: absolute;
    top: 200px;
    right: 0;
    z-index: 999;
    max-width: 350px;
}

.footer {
    padding: 130px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-nav .item-link {
    font-size: 18px;
    line-height: 21px;
}

.footer .logo {
    margin-bottom: 43px;
}

.footer-nav {
    margin-bottom: 28px;
}

.footer-text,
.footer-link {
    font-size: 18px;
    line-height: 21px;
    color: #808080;
    text-decoration: none;
}

.button {
    color: #FFFFFF;
    min-width: 260px;
    padding: 20px;
    text-align: center;
    border: 2px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 6px;
    text-decoration: none;
}
.footer-text {
    margin-bottom: 30px;
    margin-top: 0;
}


@media (max-width: 960px) {
    .rocket {
        max-width: 260px;
        right: 60px;
    }

    .table-column {
        width: 250px;
    }
}

@media (max-width: 768px) {
    .header {
        flex-direction: column;
        align-items: center;
    }

    .header .logo {
        margin-bottom: 20px;
    }

    .header .main-nav {
        margin-bottom: 20px;
    }

    .rocket {
        max-width: 180px;
        right: 50px;
    }

    .video {
        width: 100%;
    }
}

@media (max-width: 576px) {
    .table-column {
        font-size: 12px;
        width: 150px;
    }

    .features {
        padding: 20px;
    }

    .rocket {
        max-width: 180px;
        right: -100px;
    }

    .footer-nav .list {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    .footer-text {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    .footer-nav .item {
        margin-bottom: 10px;
    }

    .footer{
        align-items: center;
    }
}